<template>
    <b-row>
		<b-col cols="3"  md="4" class="product " style="margin:auto 0">
            <span>{{cartItem.name}}</span>
			<img :src="cartItem.src" alt="">
		</b-col>
        <b-col cols="2" md="1" style="margin:auto 0">
            <select name="" id="">
                <option value="S">S</option>
                <option value="M">M</option>
                <option value="L">L</option>
                <option value="XL">XL</option>
            </select>

        </b-col>
		<b-col cols="2" md="2" style="margin:auto 0">{{ cartItem.price }}</b-col>
		<b-col cols="2" md="1" style="margin:auto 0">
			<input type="number" class="form-control text-center"
				:value="cartItem.quantity"
				@input="updateQuantity"
				min="0"
                >
		</b-col>
		<b-col cols="2" md="3"  style="margin:auto 0" class="text-center">{{ subtotal }}</b-col>
		<b-col cols="1"  style="margin:auto 0" >
			<i style="border-style:none;color:red;cursor:pointer" @click="removeItem"><fas icon="trash"/></i>
		</b-col>
	</b-row>
</template>

<script>
import {mapActions} from 'vuex'
export default {
    props:['cartItem'],
    methods:{
        ...mapActions(['updateCart','removeItemInCart']),
        updateQuantity(e){
            let vm = this;
            this.updateCart({
                item:vm.cartItem,
                quantity : parseInt(e.target.value),
                isAdd:false
            })
        },
        removeItem(){
            let vm=this;
            this.removeItemInCart({
                item:vm.cartItem
            })
        }
    },
    computed:{
        subtotal(){
            return this.cartItem.price * this.cartItem.quantity;
        }
    }
}
</script>

<style lang="scss" scoped>
*{
    margin:0;
    padding:0;
    text-align:center;
    font-size:16px
}

.product{
    display:flex;
    
    img{
        width:100px;
        height:auto;
        
    }
    span{
        margin:auto 0;
    }
}
td{
    
    text-align:center;
}
.row{
    padding:15px 0;
}

@media screen and(max-width:576px) {
    .product{
        display:block;
        img{
            width:70px;
            padding-top:10px
        }
    }
    *{font-size:13px}
}
</style>