            <div><img src="@/assets/Images/slick1.jpg" alt=""></div>
            <div><img src="@/assets/Images/slick2.jpg" alt=""></div>
            <div><img src="@/assets/Images/slick3.jpg" alt=""></div>
            </VueSlickCarousel>
            <div class="top_deal">
                <h1><a  href="/">Sn phm bn chy</a></h1>
                <b-row style="padding: 0 5%">
                    <b-col cols="6" lg="3" v-for="(item,index) in discount" :key="index" class="product_item"  >
                        <router-link :to="'/Productsdetail/' + item.id" >
                            <img :src="item.src" alt=""> 
                        </router-link>
                        <div class="iconadd">
                                    <small>{{item.price}}</small>
                                    <i @click="addCart(item.id)"><fas icon="cart-plus"/></i>
                        </div>
                        <router-link :to="'/Productsdetail/' + item.id">
                                <p>{{item.name}}</p>
                        </router-link>
                        
                    </b-col>
                </b-row>
            </div>
            <div class="new_product">
                <h1><a href="/">Hng mi v</a></h1>
                <b-row style="padding: 0 10%">
                    <VueSlickCarousel :arrows="true" :dots="true" :autoplay="true" :infinity="true" :slidesToShow="3" :slidesToScroll="3" style="padding-bottom:40px !important" >
                            <b-col cols="2" lg="4" v-for="(item,index) in discount" :key="index" class="product_item"   >
                                <router-link :to="'/Productsdetail/' + item.id" >
                                        <img :src="item.src" alt=""> 
                                </router-link>
                                <div class="iconadd">
                                            <small>{{item.price}}</small>
                                            <i @click="addCart(item.id)"><fas icon="cart-plus"/></i>
                                </div>
                                <router-link :to="'/Productsdetail/' + item.id">
                                        <p>{{item.name}}</p>
                                </router-link>
                            </b-col>
                    </VueSlickCarousel>
                </b-row>
            </div>
            <div class="news">
                <h1><a href="/">Tin tc</a></h1>
                <b-row style="padding:0 5%">
                    <b-col cols="12" md="4" v-for="(neww,index) in news" :key="index" style="padding:2%">
                        <router-link to="/productsdetail">
                            <img :src="neww.src" alt="" style="height:240px "></router-link>
                            <small>{{neww.dated}}</small>
                            <a href="/"><h6>{{neww.title}}</h6></a>
                            <p>{{neww.description}}</p>
                    </b-col>
                </b-row>
            </div>
            
        </div>
</template>

<script>
	import VueSlickCarousel from 'vue-slick-carousel'
    import 'vue-slick-carousel/dist/vue-slick-carousel.css'
    import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
    import {mapActions} from 'vuex';
export default {
    name: 'MyComponent',
    components: { VueSlickCarousel },
    data(){
        return{
        
        }
    },
    computed:{
        discount(){
            let filterArr = this.$store.state.cartItems.filter((item)=>{
                if(item.discount == true)
                return item
            })
            if(filterArr.length>0){
                return filterArr
            }
            return{}
        },
        news(){
            return this.$store.state.news;
        }
    },
    methods:{
        ...mapActions(['updateCart']),
        addCart(id){
            let itemcart = this.$store.state.cartItems.filter((item)=>{
                    if(item.id ==id)
                    return item
            })
            const order ={
                item : itemcart[0],
                
                quantity:1,
                isAdd:true,
                
            };
            alert("Sn phm  c thm vo gi hng ")
            this.updateCart(order);
            
            
        }
        
    }
    
}
</script>

<style lang="scss" scoped>
