<template>
    <div class="product">
            <b-row class=" location_item" >
                
                    <p>Trang ch / Sn phm / Thi trang nam / {{location}}</p>
            </b-row>
            
            <div class="adjustment">
                
                    <h3>{{location}}</h3>
                
                    <button>
                        <span>{{choosenadjust}}</span>
                        <i @click="adjustArr ^=true">
                            <fas icon="chevron-circle-down"/>
                            <ul v-show="adjustArr==true" class="adjusthidden">
                                <li @click="choosen">Th t mc nh</li>
                                <li @click="choosenincre">Gi thp n cao </li>
                                <li @click="choosendecre">Gi cao n thp</li>
                            </ul>
                        </i>
                    </button>
                
            </div>
            <b-row >
                <b-col cols="6" md="4" lg="3" v-for="(item,index) in itemm" :key="index" class="product_item">
                    <router-link :to="'/Productsdetail/' + item.id">
                            <img :src="item.src" alt=""> 
                    </router-link>
                    <div class="iconadd">
                                <small>{{item.price}}</small>
                                <i @click="addCart(item.id)"><fas icon="cart-plus"/></i>
                    </div>
                    <router-link :to="'/Productsdetail/' + item.id">
                            <p>{{item.name}}</p>
                    </router-link>
                            
                    
                    
                </b-col>
                
                
            </b-row>
        
    </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
    data(){
        return {
            adjustArr: false,
            choosenadjust:'Th t mc nh',
        }
    },
    methods:{
        choosen(){
            this.choosenadjust = 'Th t mc nh'
            this.$router.go();
        },
        choosendecre(){
            this.choosenadjust = 'Gi cao n thp'
            function compare(a, b) {
            if (a.price < b.price)
                return 1;
            if (a.price > b.price)
                return -1;
            return 0;
            }
            return this.itemm.sort(compare);
        },
        choosenincre(){
            this.choosenadjust = 'Gi thp n cao'

            function compare(a, b) {
            if (a.price < b.price)
                return -1;
            if (a.price > b.price)
                return 1;
            return 0;
            }

            return this.itemm.sort(compare);
        },
        ...mapActions(['updateCart']),
        addCart(id){
            let itemcart = this.$store.state.cartItems.filter((item)=>{
                    if(item.id ==id)
                    return item
            })
            const order ={
                item : itemcart[0],
                
                quantity:1,
                isAdd:true,
                
            };
            alert("Sn phm  c thm vo gi hng ")
            this.updateCart(order);
            
            
        }
    },
    computed:{
        itemm(){
            
            let id = this.$route.params.id.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, '').replace(/\s/g, '').slice(0,-3);
            let filterArray = this.$store.state.cartItems.filter((item)=>{
                if(item.id.search(id)!=-1)
                return item
            })
            
            return filterArray
            
        },
        location(){
            return this.$route.params.id
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '@/assets/Scss/ProductPerson.scss' 
</style>